<template>
    <div style="position: relative;display: flex;height: 100vh;">
        <div>
            <el-button @click="numbers++">按钮+</el-button>{{ numbers }}
        </div>
        <div style="width: 80vw;border: 1px solid #000;overflow: auto;" >
            <scrolltoA id="pagePatient" ref="pagePatient"></scrolltoA>
            <scrolltoB id="diag" ref="diag"></scrolltoB>
            <scrolltoC id="pageOne" ref="pageOne"></scrolltoC>
            <scrolltoD id="oper" ref="oper"></scrolltoD> 
        </div>
        <div style="width: 20vw;position: sticky;top:10px;right: 1px;border: 1px solid #000;">
                  <el-button @click="scrollTo ('pagePatient')">跳转到患者信息</el-button>
                  <br>
                  <el-button @click="scrollTo('diag')">跳转到诊断</el-button>
                  <br>
                  <el-button @click="scrollTo('pageOne')">跳转到疾病信息</el-button>
                  <br>                           <el-button @click="scrollTo('oper')">跳转到手术</el-button>
                  <br>
                  <el-button @click="scro('pagePatient')">跳转到患者信息</el-button>
                  <br>
                 <el-button @click="scro('diag')">跳转到诊断</el-button>
                 <br>
                 <el-button @click="scro('pageOne')">跳转到疾病信息</el-button>
                 <br>
                 <el-button @click="scro('oper')">跳转到手术</el-button>
            </div>
    </div>
</template>

<script>
import scrolltoA from '@/components/Scrollto/scrolltoA.vue'
import scrolltoB from '@/components/Scrollto/scrolltoB.vue'
import scrolltoC from '@/components/Scrollto/scrolltoC.vue'
import scrolltoD from '@/components/Scrollto/scrolltoD.vue'
export default {
    components:{
        scrolltoA,
        scrolltoB,
        scrolltoC,
        scrolltoD
    },
    data () {
        return {
            numbers:0
 
        }
    },
    mounted(){
      this.Llqonclose()
    },      
    methods:{
        scrollTo(refName) {
            console.log(111);
            // const element = this.$refs[refName].$el; // 获取到具体的DOM元素
            // const offsetTop = element.offsetTop; // 获取元素距离页面顶部的距离
            // window.scrollTo({
            //     top: offsetTop,
            //     behavior: 'smooth'
            // });
            const element = this.$refs[refName]; // 获取子组件的引用
            element.$el.scrollIntoView({ behavior: 'smooth' });
        },
        scro(e){
            if (e) {
                document.getElementById(e).scrollIntoView({ behavior: 'smooth' })
            }
        },
        //监听浏览器关闭事件
        Llqonclose(){
               // 监听页面关闭事件
        window.addEventListener('beforeunload', function (event) {
            if (this.numbers==0) {
            
            }else{
            // 取消浏览器默认关闭
            event.preventDefault();

            // // Chrome 要求在事件回调中返回一个值
            event.returnValue = '';
            // // 在这里触发你的确认或取消关闭的逻辑
            // // 例如，弹出一个对话框询问用户是否确认关闭
            var confirmationMessage = '确定要关闭页面吗？';
            event.returnValue = confirmationMessage; // Chrome 要求在事件回调中返回值
            return  confirmationMessage
            // // 返回确认或取消关闭的消息
            // return confirmationMessage;
        }
        });
        },
        open() {
                this.$confirm('是否关闭浏览器', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                this.$message({
                    type: 'success',
                    message: '关闭成功!'
                });
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消关闭'
                });          
                });
        }
    }

}
</script>
 
<style lang = "scss" scoped>
// 自定义滚动条 
 ::-webkit-scrollbar 
 {
// 纵向滚动条的宽 
 width: 10px;
// 横向滚动条的高 
// 图片比宽度大就会自动产生滚动条 
height: 10px;  }

//  滚动条下方的底色 
 ::-webkit-scrollbar-track 
 {
 border-radius: 10px;
 background: #eee;
        }
// 滚动条上方的滑块
::-webkit-scrollbar-thumb {
 border-radius: 10px;
 background: #ccc;
        }
</style>